/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after, q:before, q:after {
    content: '';
    content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
body {
  cursor: default;
  font-family: Helvetica Neue, Helvetica, Roboto, Arial, sans-serif;
  font-size: 16px;
  padding: 0 50px;
}
.main {
  margin-left: auto;
  margin-right: auto;
  max-width: 1000px;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity .5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.row {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 50% 50%;
      grid-template-columns: 50% 50%;
}
.column {
  margin-left: auto;
  margin-right: auto;
  max-width: 375px;
  padding: 0 30px;
  width: 100%;
}
.controls, .log {
  border: 1px solid #ccc;
  box-shadow: 0 3px 6px #ccc;
  margin-top: 30px;
  padding: 10px;
  text-align: center;
}
.turn {
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 20px;
  margin-top: 20px;
}
.log ul {
  font-weight: bold;
  list-style: none;
  text-transform: uppercase;
}
.log ul li {
    margin: 5px;
    padding: 5px;
}
.log ul .player-turn {
    background: #e4e8ff;
    color: blue;
}
.log ul .monster-turn {
    background: #ffc0c1;
    color: red;
}
.button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: #eee;
  border: none;
  border-radius: 3px;
  box-shadow: 0 1px 1px black;
  cursor: pointer;
  font-size: 20px;
  margin: 10px;
  padding: 12px;
  text-transform: uppercase;
  transition: background-color 200ms ease;
}
.button:hover {
    transition-duration: 100ms;
}
.button--start {
  background: #aaffb0;
}
.button--start:hover {
    background-color: #76ff7e;
}
.button--attack {
  background: #ff7367;
}
.button--attack:hover {
    background-color: #ff3f43;
}
.button--special-attack {
  background: #ffaf4f;
}
.button--special-attack:hover {
    background-color: #ff9a2b;
}
.button--heal {
  background: #aaffb0;
}
.button--heal:hover {
    background-color: #76ff7e;
}
.button--give-up {
  background: #fff;
}
.button--give-up:hover {
    background-color: #c7c7c7;
}
.button:disabled {
  background: #eee;
  cursor: default;
}



